html,body
{
    margin: 0px;
    padding: 0px;
    overflow-x: hidden; 
}

.whole{
    height:100vh;
    padding:3rem;
}

.title{
    color:#303779;
    font-size:72px;
    font-family: 'Signika', sans-serif;
    text-align:center;
    margin-bottom:0.5em;
}

.cardHolder{
    display:flex;
    margin:3vh;
    height:70%;
    border-radius:20px;
    background-color:#303779;
    justify-content: space-evenly;
    padding-top:2em;
    padding-bottom:2em;

}

.subHead1{
    font-size:2rem;
    margin:3vh;
    font-family:signika, sans-serif;
    color:rgb(77, 75, 78);
}
.subHead2{
    font-size:1.5rem;
 
    font-family:signika, sans-serif;
    color:rgb(77, 75, 78);
    width:95%;
    text-align:right;
    display:flex;

    justify-content:flex-end;


    
}

.icon{
    margin:auto 0.3em;
    margin-right:0px;
    font-size:1.5em;
    margin-top:0.1em;
}

card{
    height: 100%;
}

@media (max-width:1000px){
    .whole{
        height: auto;
    }
    .subHead1{
        text-align:center;
    }

    .subHead2{
        justify-content:center;
        font-size:1rem;
    }


    .cardHolder{
        width:80vw;
        flex-direction: column;
        justify-content: center;
        align-items:center;
        margin-left:auto;
        margin-right:auto;
      
        
        }
}

@media (max-width:500px){
    .cardHolder{
        
        margin-left: -10px;
        margin-right: auto;
        
        }
}